#user {
  padding: 30px 0;
}
#user .content {
  background-color: #fff;
  min-height: 84vh;
  padding: 12px 52px;
  box-sizing: border-box;
  position: relative;
  padding-bottom: 80px;
}
#user .content .ant-input-affix-wrapper .ant-input:not(:last-child) {
  padding-left: 30px;
  padding-right: 0;
}
#user .content .ant-input-affix-wrapper .ant-input-suffix {
  right: auto;
  left: 10px;
}
#user .content .head {
  padding-top: 20px;
}
#user .content .head .time {
  display: inline-block;
  border-radius: 4px;
  border: 1px solid #999999;
  color: #333333;
  margin-bottom: 25px;
  margin-right: 57px;
  padding: 2px 4px;
  box-sizing: border-box;
}
#user .content .head .ant-input {
  border: none;
  color: #333333;
}
#user .content .head .ant-input:focus {
  box-shadow: none;
}
#user .content .head .dept {
  display: inline-block;
}
#user .content .head .dept .ant-select-selection {
  border-radius: 4px;
  border: 1px solid #999999;
}
#user .content .list {
  padding: 20px 0;
  padding-bottom: 100px;
  position: relative;
  min-height: 60vh;
  box-sizing: border-box;
}
#user .content .list .flexBox {
  display: flex;
  align-items: center;
}
#user .content .list .flexBox .flex {
  flex: 1;
}
#user .content .list .list_head,
#user .content .list .list_info {
  padding: 10px 0;
  padding-left: 16px;
  box-sizing: border-box;
  border-bottom: 1px solid #f0f0f0;
}
#user .content .list .list_head .tel,
#user .content .list .list_info .tel,
#user .content .list .list_head .name,
#user .content .list .list_info .name,
#user .content .list .list_head .type,
#user .content .list .list_info .type {
  color: #333;
  font-weight: 600;
}
#user .content .list .list_head .time,
#user .content .list .list_info .time {
  color: #333;
  font-weight: 600;
}
#user .content .list .list_info .tel,
#user .content .list .list_info .name,
#user .content .list .list_info .type,
#user .content .list .list_info .time {
  font-weight: normal;
}
#user .content .pagination {
  display: flex;
  padding: 10px 0;
  align-content: center;
  justify-content: center;
  position: absolute;
  bottom: 20px;
  width: 100%;
  left: 0;
}
#user .content .pagination span {
  display: inline-block;
  padding: 8px;
  cursor: pointer;
}
